<template>
  <g-body-layout
    :pageIndex="pageNumber"
    :pageSize="pageSize"
    :total="total"
    :filterText.sync="filterText"
    @pageIndexChange="pageIndexChange"
    @pageSizeChange="pageSizeChange"
    @suffixSearchCallback="onFilterReady"
  >
    <!-- 按钮组插槽 -->
    <template #btnGroup>
      <g-btn-group :buttonLists="btnList" @btnCallBack="btnGroupClick">
      </g-btn-group>
    </template>
    <!-- g-ndo-table插槽 -->
    <template #table>
      <g-ndo-table
        :id="id"
        :handName="'批次等级'"
        :dataList="dataList"
        :isTableLoading="loading"
        :total="total"
        @rowClick="rowClick"
      ></g-ndo-table>
    </template>
    <!-- 详细内容插槽 -->
    <template #content>
      <create-or-edit-batch-level
        :ndoEditParams="ndoEditParams"
        :refreshTag="refreshTag"
        :saveTag="saveTag"
        :resetTag="resetTag"
        @childRefresh="childRefresh($event)"
        @rowClick="rowClick($event)"
        v-if="isShowEdit"
      ></create-or-edit-batch-level>
      <g-empty v-else> </g-empty>
    </template>
  </g-body-layout>
</template>

<script>
import CreateOrEditBatchLevel from "./create-or-edit-container-level/create-or-edit-container-level.vue";
import GCopyNdo from "@/components/g-copy-ndo/g-copy-ndo.vue";
import { GNdoPageBase } from "@/utils";

export default {
  name: "BatchLevel",
  mixins: [GNdoPageBase],
  components: {
    CreateOrEditBatchLevel,
  },

  data() {
    return {
      isTableLoading: false,
      filterText: "",
    };
  },

  mounted() {},
  created() {
    this.getBtnList();
  },
  methods: {
    getBtnList() {
      // 获取按钮列表
      this.btnList = [
        {
          text: this.AppConst.button.create,
          icon: this.AppConst.icon.create,
          eventName: this.AppConst.button.create,
          display: true,
        },
        {
          text: this.AppConst.button.copy,
          icon: this.AppConst.icon.copy,
          eventName: this.AppConst.button.copy,
          display: !!this.id,
        },
        {
          text: this.AppConst.button.delete,
          icon: this.AppConst.icon.delete,
          eventName: this.AppConst.button.delete,
          display: !!this.id,
        },
        {
          text: this.AppConst.button.refresh,
          icon: this.AppConst.icon.refresh,
          eventName: this.AppConst.button.refresh,
          right: true,
          display: this.isShowEdit,
        },
        {
          text: this.AppConst.button.save,
          icon: this.AppConst.icon.save,
          eventName: this.AppConst.button.save,
          right: true,
          type: "primary",
          display: this.isShowEdit,
        },
      ];
    },
    copy() {
      this.createModal(
        { title: "复制", width: "600px" },
        {
          component: GCopyNdo,
          props: {
            entity: {
              copyValue: this.dataList[0].name,
              value: this.dataList[0].name,
            },
          },
          on: {
            success: (res) => {
              console.log(res);
              // 手动延时
              this.loading = true;
              setTimeout(() => {
                this.loading = false;
                this.copySuccess(res.value, res.copyValue);
                this.refreshGoFirstPage();
              }, 500);
            },
          },
        }
      );
    },
    delete() {
      // 删除确认
      this.$confirm({
        title: "确认删除",
        content: "确认删除该批次等级吗？删除后无法恢复。",
        onOk: () => {
          this.loading = true;
          setTimeout(() => {
            this.loading = false;
            // 消息提示
            this.deleteSuccess();
            // 刷新
            this.refresh();
          }, 500);
        },
      });
    },
    fetchData(request) {
      this.loading = true;
      setTimeout(() => {
        request.finishedCallback();
        // 填充dataList 示例数据
        let dataList = [];
        const levelTypes = [
          "质量等级",
          "工艺等级",
          "性能等级",
          "安全等级",
          "环保等级",
          "其他等级"
        ];
        
        const statuses = ["启用", "禁用"];
        
        for (let i = 0; i < 25; i++) {
          const typeIndex = i % 6;
          let levelCode, name, description;
          
          // 根据等级类型生成不同的等级数据
          switch(typeIndex) {
            case 0: // 质量等级
              const qualityLevels = [
                { code: "A", name: "A级", desc: "最高质量标准" },
                { code: "B", name: "B级", desc: "良好质量标准" },
                { code: "C", name: "C级", desc: "一般质量标准" },
                { code: "D", name: "D级", desc: "次品" }
              ];
              const qualityIndex = i % 4;
              levelCode = qualityLevels[qualityIndex].code;
              name = qualityLevels[qualityIndex].name;
              description = qualityLevels[qualityIndex].desc;
              break;
            
            case 1: // 工艺等级
              const processLevels = [
                { code: "P1", name: "工艺一级", desc: "最高工艺要求" },
                { code: "P2", name: "工艺二级", desc: "标准工艺要求" },
                { code: "P3", name: "工艺三级", desc: "基础工艺要求" }
              ];
              const processIndex = i % 3;
              levelCode = processLevels[processIndex].code;
              name = processLevels[processIndex].name;
              description = processLevels[processIndex].desc;
              break;
            
            case 2: // 性能等级
              const performanceLevels = [
                { code: "H", name: "高性能", desc: "高性能要求" },
                { code: "M", name: "中性能", desc: "中等性能要求" },
                { code: "L", name: "低性能", desc: "基础性能要求" }
              ];
              const performanceIndex = i % 3;
              levelCode = performanceLevels[performanceIndex].code;
              name = performanceLevels[performanceIndex].name;
              description = performanceLevels[performanceIndex].desc;
              break;
            
            case 3: // 安全等级
              const safetyLevels = [
                { code: "S1", name: "安全一级", desc: "最高安全要求" },
                { code: "S2", name: "安全二级", desc: "标准安全要求" },
                { code: "S3", name: "安全三级", desc: "基础安全要求" }
              ];
              const safetyIndex = i % 3;
              levelCode = safetyLevels[safetyIndex].code;
              name = safetyLevels[safetyIndex].name;
              description = safetyLevels[safetyIndex].desc;
              break;
            
            case 4: // 环保等级
              const ecoLevels = [
                { code: "E1", name: "环保一级", desc: "最高环保要求" },
                { code: "E2", name: "环保二级", desc: "标准环保要求" },
                { code: "E3", name: "环保三级", desc: "基础环保要求" }
              ];
              const ecoIndex = i % 3;
              levelCode = ecoLevels[ecoIndex].code;
              name = ecoLevels[ecoIndex].name;
              description = ecoLevels[ecoIndex].desc;
              break;
            
            case 5: // 其他等级
              const otherLevels = [
                { code: "O1", name: "其他一级", desc: "其他特殊要求" },
                { code: "O2", name: "其他二级", desc: "其他一般要求" }
              ];
              const otherIndex = i % 2;
              levelCode = otherLevels[otherIndex].code;
              name = otherLevels[otherIndex].name;
              description = otherLevels[otherIndex].desc;
              break;
          }
          
          dataList[i] = {
            id: i + 1,
            levelCode: `BL${String(i + 1).padStart(4, "0")}`,
            name: name,
            levelType: levelTypes[typeIndex],
            levelCode: levelCode,
            description: description,
            status: statuses[i % 2],
            createdBy: "admin",
            createdTime: "2024-03-19 " + (10 + i % 10) + ":" + (i % 60),
          };
        }

        // filter 过滤
        if (this.filterText) {
          console.log(this.filterText);
          dataList = dataList.filter((item) => {
            return item.levelCode.indexOf(this.filterText) > -1 || 
                   item.name.indexOf(this.filterText) > -1 ||
                   item.levelType.indexOf(this.filterText) > -1 ||
                   item.description.indexOf(this.filterText) > -1;
          });
        }

        request.successCallback({
          items: dataList,
          totalCount: dataList.length * 2,
        });
      }, 1000);
    },
  },
};
</script>

<style lang="scss" scoped></style> 